.search-inputs {
  margin-top: 105px;
  display: flex;
}

.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 15px;
  height: 30px;
  width: 300px;
}

.search-icon {
  height: 60px;
  width: 50px;
  background-color: white;
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.search-icon svg {
  font-size: 25px;
  color: gray;
}

.data-result {
  margin-top: 5px;
  padding: 18px;
  width: 300px;
  height: fit-content;
  max-height: 400px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
}

.data-result::-webkit-scrollbar {
  display: none;
}

.data-result .data-item {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: black;
}

.data-item p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}

#clear-btn {
  cursor: pointer;
}